@import '~antd/dist/antd.css';

*{
  padding:0;
  margin:0;
}
ul,li{
  list-style:none;
}

body{
  background-color: rgb(212, 209, 209);
}

.header{
  height:50px;
  background-color:#000;
}

.header a{
  margin:0 20px;
  color:rgb(216, 214, 214);
}

/* logo */
.header img{
  float:left;
  width:120px;
  margin-top:12px;
}


.header input{
  float:left;
  margin-left:50px;
  margin-top:10px;
}


.main{
  width:1200px;
  margin:25px auto;
}

/* 内容 */
.content{
  width:850px;
  float:left;
  margin: 0 5px 0 0 ;
  background-color: rgb(255, 255, 255);
}

/**小标题*/
.little-content{
  position: relative;
}
.little-content li{
  border-bottom:1px solid rgb(207, 204, 204);
  margin:10px 0px 10px 0px;
}
/* 头像 */
.img{
  width:30px;
  height:30px;
}

.data{
  color:rgb(105, 3, 105);
  font-size:13px;
  margin-left: 5px;
}
.title{
  margin-right:20px;
  font-size:10px;
  color:gray;
}



